import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import {DashboardApiService} from '../dashboard-api.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  registerForm: FormGroup;
  submitted = false;
  isLoading : boolean = false;
  countryList : any = [];
  stateList : any = [];
  cityList : any = [];
  selectedCountry : string = '';

  constructor(private api : DashboardApiService, private fb: FormBuilder) {
    this.registerForm = this.fb.group({
      first_name: ['', [Validators.required]],
      last_name: ['', [Validators.required]],
      email: ['', [Validators.required, Validators.email]],
      address1: ['', [Validators.required]],
      address2: ['', [Validators.required]],
      country: ['', [Validators.required]],
      state: ['', [Validators.required]],
      city: ['', [Validators.required]]
    });
  }

  ngOnInit(): void {
    this.listCountries();
  }

  listCountries(){
    this.api.listCountries().subscribe((response) => {
      this.countryList = response;
      this.registerForm.controls['state'].setValue("");
      this.registerForm.controls['city'].setValue("");
      this.stateList = [];
      this.cityList = [];
    },(error) => {
      console.log(error);
    })
  }

  onCountryChange(event){
    let params = {
      "country_code" : event.target.value
    };
    this.selectedCountry = event.target.value;
    this.api.listStates(params).subscribe((response) => {
      this.stateList = response;
      this.registerForm.controls['state'].setValue("");
      this.registerForm.controls['city'].setValue("");
      this.cityList = [];
    },(error) => {
      console.log(error);
    })
  }

  onStateChange(event){
    let params = {
      "country_code" : this.selectedCountry,
      "state_code": event.target.value
    };

    this.api.listCities(params).subscribe((response) => {
      this.cityList = response;
      this.registerForm.controls['city'].setValue("");
    },(error) => {
      console.log(error);
    })
  }

  get f() {
    return this.registerForm.controls;
  }

  onRegister(){
    this.submitted = true;

    if (this.registerForm.invalid) {
      return;
    }

    const { first_name, last_name ,email, address1, address2, country, state, city } = this.registerForm.value;
    const params = {
      first_name,
      last_name,
      email,
      address1,
      address2,
      country,
      state,
      city
    }
    this.isLoading = true;
    this.api.register(params).subscribe((response :any) => {
      console.log(response);
      this.isLoading = false;
      alert(response.message);

    },(error) => {
      console.log(error);
    })
  }

}
