import { Component, OnInit } from '@angular/core';
import { DashboardApiService } from '../dashboard-api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  message : string = "Welcome Thiru!";
  accountNo : number = 457869522;
  isValid : boolean = true;
  isPass : any;

  color : string = 'red';

  isActive : boolean = true;

  login : any = {
    name : '',
    job : ''
  }

  loading : boolean = true;

  userList : any = [];

  constructor(private api : DashboardApiService, private router : Router) { }

  ngOnInit(): void {
    this.onLoadUserList();
  }

  onLoadUserList(){
    this.loading = false;
    this.api.listOfUsers().subscribe((response : any) => {
      this.loading = true;
        console.log(response.data)
        this.userList = response.data;
      // this.userList = [];
    }, (err) => {
      console.log(err)
    })
  }

  onClickButton(){
    this.loading = false;
    this.api.createUser(this.login).subscribe((response) => {
      this.loading = true;
      console.log(response)
      this.router.navigateByUrl('/register');
    }, (err) => {
      this.loading = true;
      console.log(err)
    })
  }

}
