import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DashboardApiService {

  basePath : string = "https://reqres.in/api/";
  apiPath : string = "https://concord-test.herokuapp.com/api/";

  constructor(private http : HttpClient) { }

  listOfUsers(){
    const url : string = this.basePath + "users?page=2";
    return this.http.get(url);
  }

  createUser(data){
    const url : string = this.basePath + 'users';
    return this.http.post(url, data);
  }

  listCountries(){
    const url : string = this.apiPath + "countries";
    return this.http.get(url);
  }

  listStates(data){
    const url : string = this.apiPath + 'states';
    return this.http.post(url, data);
  }

  listCities(data){
    const url : string = this.apiPath + 'cities';
    return this.http.post(url, data);
  }

  register(data){
    const url : string = this.apiPath + 'register';
    return this.http.post(url, data);
  }
}
